@import "smart-grid";
@import "fonts.css";
@import "vars";
@import "card/_card";
@import "form-block/_form-block";
@import "button/_button";
@import "input/_input";
@import "main-footer/_main-footer";


.page {
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	max-width: 100%;
	font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
	font-size: 16px;
	background-color: $routes_background;
	text-rendering: optimizeLegibility;
	text-decoration-skip: objects;
	-webkit-text-size-adjust: 100%;
	-webkit-font-smoothing: antialiased;
	-webkit-tap-highlight-color: transparent; }

.html-404 {
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	max-width: 100%;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center; }


.not-found-page {
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	background-color: #f7fcff;
	width: 100%;
	height: 100%;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

	&_text {
		font-family: "MuseoSansCyrlRegular", Arial, sans-serif;
		font-size: 28px;
		line-height: 1;
		color: #2d2e2e;
		text-align: center;
		padding: 20px;
		box-sizing: border-box;
		max-width: 100%; } }

.img-container {
	width: 100%;
	max-width: 585px;
	box-sizing: border-box;
	padding: 0 20px;

	img {
		width: 100%;
		height: auto; } }

.text-dark {
	color: $text-dark; }

.text-light {
	color: $text-light; }

.main-offer {
	width: 100%;
	min-height: 550px;
	height: 100vh;
	background: linear-gradient($main-header-blue, $main-header-light);
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	position: relative;


	&__header {
		max-width: 1000px;
		width: 100%;
		margin-top: 7%;
		margin-right: 20px;
		margin-left: 20px;
		opacity: 1;
		transition: transform .7s ease-out, opacity .7s ease-out;
		display: flex;
		flex-direction: row;
		justify-content: center; }




	&__image {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;

		img {
			display: block;
			width: 100%;
			height: auto; } } }

.main-text {
	width: 90%; }



.main-offer-animation {
	transform: translateY(200px);
	opacity: 0; }


.main-header {
	width: 100%;
	min-height: 100px;
	padding: 0 25px;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	align-items: center;
	flex-wrap: wrap;

	&__nav {
		margin-left: auto;
		margin-right: 70px;

		@include md-block {
			display: none; }

		&_item {
			position: relative;
			text-decoration: none;
			margin: 0 25px;
			z-index: 1;
			overflow-x: hidden;
			display: inline-block;


			&:hover::before {

				transform: translateX(100%); }


			&::before {
				position: absolute;
				content: '';
				width: 100%;
				height: 40%;
				background-color: $main_yellow;
				bottom: 0;
				z-index: -1;
				left: -100%;
				transition: transform .4s ease-out; } } } }


.login-user {
	width: 20px;
	height: 20px;
	display: inline-block; }

.search-form {
	width: 20px;
	height: 20px;
	display: inline-block; }


.routes-wrap {
	@include wrapper(); }

.routes {
	position: relative;
	padding: 130px 0;
	background: url('../img/routes_bg.jpg') $routes_background no-repeat center;
	background-size: cover;

	&::before {
		position: absolute;
		content: '';
		width: 100%;
		height: 277px;
		background: url('../img/brush.png') transparent no-repeat center;
		background-position: center;
		top: 50%; } }



.header {
	font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
	font-size: 48px;
	line-height: 48px; }

.header_margin-bottom_80 {
	margin-bottom: 80px; }

.user-block {

	@include md-block {
		display: none; }

	&__item {

		&:not(:last-child) {
			margin-right: 30px; } } }


.login-user {

	&:hover {
		animation: twitch .4s ease-out; } }


.search-form {
	position: relative;
	z-index: 1;

	&::before {
		position: absolute;
		content: '';
		background-color: $main_yellow;
		border-radius: 50%;
		width: 100%;
		height: 100%;
		z-index: -1;
		transform: scale(0);
		opacity: 0;
		transition: transform .4s ease-out, opacity .4s ease-out; }

	&:hover::before {
		transform: scale(2);
		opacity: 1; } }


.mobile-menu {
	margin-left: auto;
	display: none;

	@include md-block {
		display: block; } }

.mobile-menu-item {
	fill: #fff; }

.mobile-nav {
	position: relative;
	width: 100%;
	height: 100%;
	position: fixed;
	background-color: $blue;
	z-index: 999;
	padding: 40px 20px;
	box-sizing: border-box;
	visibility: hidden;
	opacity: 0;
	transform: translateX(-100%);
	transition: transform .4s ease-out; }

.mobile-nav_active {
	visibility: visible;
	opacity: 1;
	transform: translateX(0);
	transition: transform .4s ease-out; }

.mobile-nav-container {
	display: flex;
	flex-direction: column; }

.mobile-nav__item {
	text-decoration: none;

	&:not(:last-child) {
		margin-bottom: 30px; } }

.mobile-nav__close {
	position: absolute;
	width: 20px;
	height: 20px;
	top: 20px;
	right: 20px;
	z-index: 1000;


	&::before {
		content: '';
		position: absolute;
		background-color: #fff;
		width: 20px;
		height: 3px;
		transform: rotate(45deg); }


	&::after {
		content: '';
		position: absolute;
		background-color: #fff;
		width: 20px;
		height: 3px;
		transform: rotate(-45deg); } }



@keyframes twitch {
	0% {
		transform: rotate(0deg);
		transform-origin: 50% 100% 0; }

	30% {
		transform: rotate(-20deg);
		transform-origin: 50% 100% 0; }

	60% {
		transform: rotate(20deg);
		transform-origin: 50% 100% 0; }
	100% {
		transform: rotate(0deg);
		transform-origin: 50% 100% 0; } }
