@import url(fonts.css);

.card-flex {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.card {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.card-wrapper {
  width: 81%;
  height: 100%;
  color: #292309;
}

.card-wrapper:hover .card__image {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.card-wrapper:hover .card__image::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  transform: translateX(0);
  background-color: #fcdc54;
  transition: transform .6s ease-in-out;
}

.card-wrapper:hover .card__descr {
  transform: translateX(100px);
}

.card__header {
  font-size: 36px;
  margin-top: 0;
  margin-bottom: 24px;
}

.card__descr {
  position: absolute;
  left: 0;
  top: 60%;
  z-index: 2;
  transform: translateX(0);
  transition: transform .6s ease-in-out;
}

.card__image {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #fff;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  transition: box-shadow .4s ease-out;
  overflow: hidden;
}

.card__image::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #fcdc54;
  transform: translateX(-100%);
  transition: transform .6s ease-in-out;
}

.card__image img {
  width: 100%;
  height: 100%;
}

.card_normal {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(33.33333% - 20px);
}

.form-block {
  padding: 90px 0 120px;
}

.form-block-container {
  max-width: 1170px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.form {
  position: relative;
  padding: 45px 40px;
  background-color: #54a6e6;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  transition: box-shadow .4s ease-out;
}

.form:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.form__header {
  font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
  font-size: 36px;
  line-height: 1;
  margin-top: 0;
  margin-bottom: 22px;
  letter-spacing: 1px;
}

.form__descr {
  font-size: 16px;
  line-height: 1;
  margin-bottom: 40px;
}

.form__mountain {
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.scene-cloud {
  position: absolute;
  width: 400px;
  height: 200px;
  background: url("../img/cloud.png") transparent no-repeat center;
  top: 0;
  right: 2%;
}

.form-input-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.button {
  background-color: #fcdc54;
  border: none;
  padding: 24px 43px;
  border-radius: 5px;
  color: #292309;
  text-decoration: none;
  font-size: 16px;
  font-family: "MuseoSansCyrlRegular", Arial, sans-serif;
  box-sizing: border-box;
  letter-spacing: 0.2px;
  transition: transform .4s ease-out, background-color .4s ease-out;
}

.button:hover {
  cursor: pointer;
  background-color: #ffe578;
  transform: translateY(-2px);
}

.button-404 {
  max-width: 300px;
}

.input {
  font-size: 16px;
  font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
  border-radius: 5px;
  border: none;
  box-sizing: border-box;
  width: 240px;
  padding: 23px;
  margin-right: 20px;
  transition: transform .4s ease-out;
  height: 67px;
}

.input:placeholder {
  color: #a7a7a7;
}

.input:hover {
  transform: translateY(-2px);
}

.input:focus {
  border-radius: 5px;
  border: 2px solid #fcdc54;
}

.main-footer {
  width: 100%;
  box-sizing: border-box;
  background-color: #54a6e6;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  padding: 80px 30px;
}

.main-footer__rights {
  opacity: .6;
  margin-left: 80px;
}

.main-footer__link {
  text-decoration: none;
  margin: 0 25px;
  position: relative;
  z-index: 1;
  overflow-x: hidden;
  text-align: center;
}

.main-footer__link:hover::before {
  transform: translateX(100%);
}

.main-footer__link::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 40%;
  background-color: #fcdc54;
  bottom: 0;
  z-index: -1;
  left: -100%;
  transition: transform .4s ease-out;
}

.main-footer__social {
  margin-left: 90px;
  display: flex;
}

.main-footer__social_link {
  width: 20px;
  height: 20px;
}

.main-footer__social_link:hover {
  animation: twitch .4s ease-out;
}

.main-footer__social_link:not(:last-child) {
  margin-right: 30px;
}

.main-footer-logo-container {
  margin-right: auto;
  display: flex;
  flex-direction: row;
}

.main-footer-link-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.page {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  max-width: 100%;
  font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
  font-size: 16px;
  background-color: #f8f8f8;
  text-rendering: optimizeLegibility;
  text-decoration-skip: objects;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}

.html-404 {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.not-found-page {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: #f7fcff;
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.not-found-page_text {
  font-family: "MuseoSansCyrlRegular", Arial, sans-serif;
  font-size: 28px;
  line-height: 1;
  color: #2d2e2e;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  max-width: 100%;
}

.img-container {
  width: 100%;
  max-width: 585px;
  box-sizing: border-box;
  padding: 0 20px;
}

.img-container img {
  width: 100%;
  height: auto;
}

.text-dark {
  color: #292309;
}

.text-light {
  color: #ffffff;
}

.main-offer {
  width: 100%;
  min-height: 550px;
  height: 100vh;
  background: linear-gradient(#8cc5f2, #bee0f3);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.main-offer__header {
  max-width: 1000px;
  width: 100%;
  margin-top: 7%;
  margin-right: 20px;
  margin-left: 20px;
  opacity: 1;
  transition: transform .7s ease-out, opacity .7s ease-out;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.main-offer__image {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.main-offer__image img {
  display: block;
  width: 100%;
  height: auto;
}

.main-text {
  width: 90%;
}

.main-offer-animation {
  transform: translateY(200px);
  opacity: 0;
}

.main-header {
  width: 100%;
  min-height: 100px;
  padding: 0 25px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.main-header__nav {
  margin-left: auto;
  margin-right: 70px;
}

.main-header__nav_item {
  position: relative;
  text-decoration: none;
  margin: 0 25px;
  z-index: 1;
  overflow-x: hidden;
  display: inline-block;
}

.main-header__nav_item:hover::before {
  transform: translateX(100%);
}

.main-header__nav_item::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 40%;
  background-color: #fcdc54;
  bottom: 0;
  z-index: -1;
  left: -100%;
  transition: transform .4s ease-out;
}

.login-user {
  width: 20px;
  height: 20px;
  display: inline-block;
}

.search-form {
  width: 20px;
  height: 20px;
  display: inline-block;
}

.routes-wrap {
  max-width: 1170px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.routes {
  position: relative;
  padding: 130px 0;
  background: url("../img/routes_bg.jpg") #f8f8f8 no-repeat center;
  background-size: cover;
}

.routes::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 277px;
  background: url("../img/brush.png") transparent no-repeat center;
  background-position: center;
  top: 50%;
}

.header {
  font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
  font-size: 48px;
  line-height: 48px;
}

.header_margin-bottom_80 {
  margin-bottom: 80px;
}

.user-block__item:not(:last-child) {
  margin-right: 30px;
}

.login-user:hover {
  animation: twitch .4s ease-out;
}

.search-form {
  position: relative;
  z-index: 1;
}

.search-form::before {
  position: absolute;
  content: '';
  background-color: #fcdc54;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  z-index: -1;
  transform: scale(0);
  opacity: 0;
  transition: transform .4s ease-out, opacity .4s ease-out;
}

.search-form:hover::before {
  transform: scale(2);
  opacity: 1;
}

.mobile-menu {
  margin-left: auto;
  display: none;
}

.mobile-menu-item {
  fill: #fff;
}

.mobile-nav {
  position: relative;
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: #54a6e6;
  z-index: 999;
  padding: 40px 20px;
  box-sizing: border-box;
  visibility: hidden;
  opacity: 0;
  transform: translateX(-100%);
  transition: transform .4s ease-out;
}

.mobile-nav_active {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
  transition: transform .4s ease-out;
}

.mobile-nav-container {
  display: flex;
  flex-direction: column;
}

.mobile-nav__item {
  text-decoration: none;
}

.mobile-nav__item:not(:last-child) {
  margin-bottom: 30px;
}

.mobile-nav__close {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 20px;
  right: 20px;
  z-index: 1000;
}

.mobile-nav__close::before {
  content: '';
  position: absolute;
  background-color: #fff;
  width: 20px;
  height: 3px;
  transform: rotate(45deg);
}

.mobile-nav__close::after {
  content: '';
  position: absolute;
  background-color: #fff;
  width: 20px;
  height: 3px;
  transform: rotate(-45deg);
}

@keyframes twitch {
  0% {
    transform: rotate(0deg);
    transform-origin: 50% 100% 0;
  }

  30% {
    transform: rotate(-20deg);
    transform-origin: 50% 100% 0;
  }

  60% {
    transform: rotate(20deg);
    transform-origin: 50% 100% 0;
  }

  100% {
    transform: rotate(0deg);
    transform-origin: 50% 100% 0;
  }
}

@media screen and (max-width: 1200px) {
  .card-wrapper:hover .card__descr {
    transform: translateX(50px);
  }

  .form-block-container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .form-input-wrapper {
    flex-direction: column;
  }

  .button {
    width: 240px;
    margin-top: 10px;
  }

  .input {
    margin-bottom: 10px;
  }

  .main-footer {
    flex-direction: column;
  }

  .main-footer__rights {
    margin-left: 0;
    margin-top: 20px;
  }

  .main-footer__link {
    margin-top: 15px;
  }

  .main-footer__social {
    margin-left: 0;
  }

  .main-footer-logo-container {
    margin-right: 0;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
  }

  .main-footer-link-container {
    margin-bottom: 40px;
  }

  .routes-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 992px) {
  .card_normal {
    width: calc(50% - 20px);
  }

  .card_normal {
    margin-bottom: 30px;
  }

  .form-block-container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .main-header__nav {
    display: none;
  }

  .routes-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }

  .user-block {
    display: none;
  }

  .mobile-menu {
    display: block;
  }
}

@media screen and (max-width: 730px) {
  .card_normal {
    width: calc(83.33333% - 20px);
  }

  .card_normal {
    margin-left: calc(8.33333% + 10px);
  }

  .card_normal {
    margin-right: calc(8.33333% + 10px);
  }

  .form-block-container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .form__mountain {
    display: none;
  }

  .button {
    width: 100%;
    margin-right: 0;
  }

  .input {
    width: 100%;
    margin-right: 0;
  }

  .routes-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .form-block-container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .routes-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 386px) {
  .card-wrapper:hover .card__descr {
    transform: translateX(0);
  }

  .card__descr {
    top: 50%;
  }

  .form-block-container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .routes-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
}