.button {
	background-color: $main_yellow;
	border: none;
	padding: 24px 43px;
	border-radius: 5px;
	color: $text-dark;
	text-decoration: none;
	font-size: 16px;
	font-family: "MuseoSansCyrlRegular", Arial, sans-serif;
	box-sizing: border-box;
	letter-spacing: 0.2px;
	transition: transform .4s ease-out, background-color .4s ease-out;

	@include lg-block {
		width: 240px;
		margin-top: 10px; }

	@include sm-block {
		width: 100%;
		margin-right: 0; }

	&:hover {
		cursor: pointer;
		background-color: #ffe578;
		transform: translateY(-2px); } }

.button-404 {
	max-width: 300px; }

