.input {

	font-size: 16px;
	font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
	border-radius: 5px;
	border: none;
	box-sizing: border-box;
	width: 240px;
	padding: 23px;
	margin-right: 20px;
	transition: transform .4s ease-out;
	height: 67px;

	@include lg-block {
		margin-bottom: 10px; }

	@include sm-block {
		width: 100%;
		margin-right: 0; }


	&:placeholder {
		color: #a7a7a7; }

	&:hover {
		transform: translateY(-2px); }

	&:focus {
		border-radius: 5px;
		border: 2px solid $main_yellow; } }

