.form-block {}

.form-block {
	padding: 90px 0 120px;

	&-container {
		@include wrapper(); } }

.form {
	position: relative;
	padding: 45px 40px;
	background-color: $blue;
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	box-shadow: 0 4px 16px rgba(0,0,0,.2);
	transition: box-shadow .4s ease-out;

	&:hover {
		box-shadow: 0 10px 20px rgba(0,0,0,.3); }


	&__header {
		font-family: "MuseoSansCyrlSemibold", Arial, sans-serif;
		font-size: 36px;
		line-height: 1;
		margin-top: 0;
		margin-bottom: 22px;
		letter-spacing: 1px; }

	&__descr {
		font-size: 16px;
		line-height: 1;
		margin-bottom: 40px; }

	&__mountain {
		position: absolute;
		right: 0;
		bottom: 0;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;

		@include sm-block {
			display: none; } } }



.scene-cloud {
	position: absolute;
	width: 400px;
	height: 200px;
	background: url('../img/cloud.png') transparent no-repeat center;
	top: 0;
	right: 2%; }

.form-input-wrapper {
	display: flex;
	flex-wrap: wrap;

	@include lg-block {
		flex-direction: column; } }

