


.card-flex {
	@include row-flex(); }



.card {
	position: relative;
	display: flex;
	justify-content: flex-end;
	align-items: flex-start;

	&-wrapper {
		width: 81%;
		height: 100%;
		color: $text-dark;

		&:hover .card__image {

			box-shadow: 0 10px 20px rgba(0,0,0,.3); }

		&:hover .card__image::before {

			position: absolute;
			content: '';
			width: 100%;
			height: 100%;
			transform: translateX(0);
			background-color: $main_yellow;
			transition: transform .6s ease-in-out; }

		&:hover .card__descr {
			transform: translateX(100px);

			@include lg-block {
				transform: translateX(50px); }

			@include xxs-block {
				transform: translateX(0); } } }



	&__header {
		font-size: 36px;
		margin-top: 0;
		margin-bottom: 24px; }

	&__descr {
		position: absolute;
		left: 0;
		top: 60%;
		z-index: 2;
		transform: translateX(0);
		transition: transform .6s ease-in-out;

		@include xxs-block {
			top: 50%; } }

	&__image {
		position: relative;
		z-index: 1;
		width: 100%;
		height: 100%;
		background-color: #fff;
		box-shadow: 0 4px 16px rgba(0,0,0,.2);
		transition: box-shadow .4s ease-out;
		overflow: hidden;

		&::before {
			position: absolute;
			content: '';
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background-color: $main_yellow;
			transform: translateX(-100%);
			transition: transform .6s ease-in-out; }



		img {
			width: 100%;
			height: 100%; } } }



.card_normal {
	@include col();
	@include size(4);

	@include size-md(6);
	@include md-block {
		margin-bottom: 30px; }


	@include size-sm(10);
	@include offset-left-sm(1);
	@include offset-right-sm(1); }



