.main-footer {
	width: 100%;
	box-sizing: border-box;
	background-color: $blue;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	flex-wrap: wrap;
	padding: 80px 30px;

	@include lg-block {
		flex-direction: column; }


	&__rights {
		opacity: .6;

		margin-left: 80px;

		@include lg-block {
			margin-left: 0;
			margin-top: 20px; } }

	&__link {
		text-decoration: none;
		margin: 0 25px;
		position: relative;
		z-index: 1;
		overflow-x: hidden;
		text-align: center;

		@include lg-block {
			margin-top: 15px; }


		&:hover::before {

			transform: translateX(100%); }


		&::before {
			position: absolute;
			content: '';
			width: 100%;
			height: 40%;
			background-color: $main_yellow;
			bottom: 0;
			z-index: -1;
			left: -100%;
			transition: transform .4s ease-out; } }

	&__social {
		margin-left: 90px;
		display: flex;

		@include lg-block {
			margin-left: 0; }



		&_link {

			width: 20px;
			height: 20px;

			&:hover {
				animation: twitch .4s ease-out; }


			&:not(:last-child) {
				margin-right: 30px; } } } }

.main-footer-logo-container {
	margin-right: auto;
	display: flex;
	flex-direction: row;

	@include lg-block {
		margin-right: 0;
		width: 100%;
		flex-direction: column;
		align-items: center;
		margin-bottom: 40px; } }

.main-footer-link-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;

	@include lg-block {
		margin-bottom: 40px; } }

